 @import url(https://fonts.googleapis.com/css?family=Covered+By+Your+Grace);

span[data-days] {
  font-size: 200px;
  color: #db4844;
}
span[data-hours] {
  font-size: 150px;
  color: #f07c22;
}
span[data-minutes] {
  font-size: 100px;
  color: #f6da74;
}
span[data-seconds] {
  font-size: 50px;
  color: #abcd58;
}
input{
    width: 300px;
    height: 30px;
    border-radius: 20px;
}
.swal2-styled {
    height: max-content;
   
}

button {
    width: 150px;
    height: 30px;
    border-radius: 20px;
}
button:disabled
{
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #ece7e7;
}
div {
  display: inline-block;
  line-height: 1;
  padding: 20px;
  font-size: 40px;
}

span {
  display: block;
  font-size: 20px;
  color: white;
}

body {
  text-align: center;
  font-family: 'Covered By Your Grace', cursive;
  color: white;
  background: #222;
  font-size: 20px;
}